import React, { useState, useEffect } from "react"
import { Row, Col, Form, Icon } from "antd"
import { Heading, Button, ButtonLink, Loading } from "components"
import { Formik } from "formik"
import Script from "react-load-script"
import TextInput from "components/forms/TextInput"
import DateInput from "components/forms/DateInput"
// import SelectInput from "components/forms/SelectInput"
import { googleApiKey } from "helpers"
import RadioInput from "components/forms/RadioInput"
import moment from "moment"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"

let autocomplete = null

function Basic({ handlers, onData, loading, i18n }) {
	const { handleNext, handlePrev, onUpdateProfile } = handlers
	const { userData = {}, setUserData } = onData
	const [addressValue, setAddressValue] = useState("")
	const [address, setAddress] = useState({})

	// const raceOptions = [
	// 	{ key: "Malay", value: "Malay", label: <Trans>Malay</Trans> },
	// 	{ key: "Chinese", value: "Chinese", label: <Trans>Chinese</Trans> },
	// 	{ key: "Indian", value: "Indian", label: <Trans>Indian</Trans> },
	// 	{ key: "Others", value: "Others", label: <Trans>Others</Trans> }
	// ]

	const handleLoad = () => {
		const options = {
			types: [],
			componentRestrictions: { country: userData.country }
		}

		/*global google*/
		autocomplete = new google.maps.places.Autocomplete(document.getElementById("location"), options)
		autocomplete.addListener("place_changed", handlePlaceSelect)
	}

	const handlePlaceSelect = () => {
		const addressObject = autocomplete.getPlace()
		const userAddress = addressObject.address_components

		if (userAddress) {
			setAddress({ location: addressObject.formatted_address })
			setAddressValue(addressObject.formatted_address)
		}
	}

	const handleSubmitBasic = values => {
		values = {
			...userData,
			...values,
			address: addressValue || userData.address,
			phone: `${userData.country === "id" ? "+62" : "+65"}${values.phone}`,
			dob: moment(values.dob).format("YYYY/MM/DD"),
			nric: ""
		}
		setUserData(values)
		onUpdateProfile(values, "curriculum")
	}

	useEffect(() => {
		if (userData.address) setAddressValue(userData.address)
	}, [userData.address])

	if (Object.keys(userData).length === 0) {
		return <Loading />
	}

	return (
		<Col lg={16} xs={24} style={{ padding: "2em 3em" }}>
			<Heading
				content={<Trans>Basic profile</Trans>}
				subheader={
					<span>
						<Trans>Tell us some of your details</Trans>
					</span>
				}
				level={4}
				marginBottom="2em"
			/>
			<Formik
				onSubmit={handleSubmitBasic}
				enableReinitialize
				initialValues={{
					...userData,
					phone: (userData.phone || "").startsWith("+") ? (userData.phone || "").slice(3) : userData.phone,
					gender: (userData.gender || "").toLowerCase(),
					race: userData.race || undefined
				}}
			>
				{({ handleSubmit }) => (
					<Form layout="vertical" onSubmit={handleSubmit}>
						<Row gutter={32}>
							<Col lg={12}>
								<DateInput
									name="dob"
									label={<Trans>Date of birth</Trans>}
									placeholder={i18n._(t`Your date of birth...`)}
									disabledDate={current => current && current > moment()}
								/>
							</Col>
							<Col lg={12}>
								<TextInput
									name="phone"
									label={<Trans>Your mobile</Trans>}
									placeholder={i18n._(t`E.g. 87888788`)}
									addonBefore={userData.country === "id" ? "+62" : "+65"}
								/>
							</Col>
						</Row>
						<Row gutter={32}>
							{/* <Col lg={12}>
								<SelectInput
									name="race"
									label={<Trans>Race</Trans>}
									placeholder={i18n._(t`Your race...`)}
									options={raceOptions}
								/>
							</Col> */}

							<Col lg={12}>
								<Script url={googleApiKey} onLoad={handleLoad} />
								<TextInput
									special
									allowClear
									id="location"
									name="address"
									placeholder={i18n._(t`Your location...`)}
									label={<Trans>Your location</Trans>}
									value={addressValue}
									onChange={e => setAddressValue(e.target.value)}
								/>
							</Col>
							<Col lg={12}>
								<RadioInput
									name="gender"
									label={<Trans>Gender</Trans>}
									options={[
										{ value: "m", label: <Trans>Male</Trans> },
										{ value: "f", label: <Trans>Female</Trans> }
									]}
								/>
							</Col>
						</Row>

						<TextInput
							textarea
							rows={4}
							name="description"
							label={<Trans>Short description about you</Trans>}
							placeholder={i18n._(
								t`E.g. I am Jenny Ang, a professional tutor from Singapore with more than 3-year teaching experience, etc`
							)}
						/>

						<br />
						<br />

						<Row type="flex" justify="space-between" align="middle">
							<Col lg={8}>
								<Trans>
									<ButtonLink size="large" htmllType="button" icon="left" onClick={() => handlePrev("Name")}>
										Back
									</ButtonLink>
								</Trans>
							</Col>
							<Col lg={8} style={{ textAlign: "right" }}>
								<Button type="primary" htmlType="submit" loading={loading}>
									<Trans>
										Next <Icon type="right" />
									</Trans>
								</Button>
							</Col>
						</Row>
					</Form>
				)}
			</Formik>
		</Col>
	)
}

export default withI18n()(Basic)
